<template>
  <div class="tongxunlu">

   	<div class="scroo" ref='scroo'>
   		<div>
   			<ul ref='ulheigth'>
   				<li v-for="(i,index) in data" class="list-group" ref='liheigth'>
   					<h2 class="list-group-title" ref="zhentitle">{{i.title}}</h2>
   					<ul>
   						<li @click='sellgeshou(it)' class="list-group-item border-1px" v-for='it in i.items'>
   							<img v-lazy="it.avatar" class="avater">
   							<span class="name">{{it.name}}</span>
   						</li>
   					</ul>
   				</li>
   			</ul>
   		</div>
   	</div>
    <div class="list-shortcut" >
      <ul >
        <li class="item" :class="{'avtive':i.title===currentIndex}"  v-for="(i,index) in data" @touchstart="onShortstars(index)">
          {{i.title=='热门' ? '热' : i.title}}
        <!--   {{currentIndex}} -->
        </li>
      </ul>
    </div>
    <div v-show='isshow11'  class="dingwei" ref='jiatitle'>
      {{currentIndex}}
  </div>
    <div v-show="!data.length" class="landing">
      <lang></lang>
    </div>
  </div>
</template>

<script>

import bcsool from 'better-scroll'
import lang from '../../base/langing/hello'
export default {
  name: 'tongxunlu',
  data () {
    return {
     listHeight : [],
     scrollY : '',
     noscrollY : '',
     isbli : true,
     isshow11 : 'true',
     diff : -1
    }

  },
  props : {
  	data : {}
  },
  components : {
    lang
  },
  created() {

  },

  methods : {
    sellgeshou(i){
      this.$emit('getgeshou',i)
    },

  	_gundong() {
  		this.soll = new bcsool(this.$refs.scroo,{
          probeType: 3,
          click: true
      })
      this.soll.on("scroll",(pos)=>{
        this.scrollY = Math.abs(Math.round(pos.y))
        this.noscrollY = Math.round(pos.y)
      })
  	},
    onShortstars(ind) {
       let lilist = this.$refs.ulheigth.getElementsByClassName('list-group');
     this.soll.scrollToElement(lilist[ind],300)
    },
    _liheigth() {
        let lilist = this.$refs.ulheigth.getElementsByClassName('list-group');
        let height = 0;
        this.listHeight.push(height);
        for(let i = 0; i < lilist.length; i++){
          let item = lilist[i]
          height += item.clientHeight;
          this.listHeight.push(height);
        }

    },

  },
  computed : {
     currentIndex() {
      for(let i =0; i<this.listHeight.length;i++){
        let height1 = this.listHeight[i];
        let height2 = this.listHeight[i+1];
        if(this.scrollY>=height1 && this.scrollY<height2){
          this.diff =height2-this.scrollY
          return this.data[i].title;
        }
      }
      return ;
    },

  },
  watch : {
  	data() {
  		setTimeout(()=>{
  			this.$nextTick(()=>{
          this. _liheigth()
        this._gundong()

  	})
  		},1)


  	},
    noscrollY(n){
      if(n>0){
        this.isshow11 = false
      }else if(n<0){
       this.isshow11 = true
      }
    },
    diff(new1) {
        let fietop = (new1>0 && new1 < 28) ? new1-28 : 0
        fietop = fietop +'px'
        this.$refs.jiatitle.style.transform =`translate3d(0,${fietop},0)`
    }

  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="stylus" rel="stylesheet/stylus" scoped>
@import "../../commnt/stylus/index"
.dingwei{
  position: fixed;
  width: 100%;
  top : 40px;
  height :28px;
  line-height :28px;
  padding-left :20px;
  background :#eee;
}
.landing{
   position: fixed;
  top : 50%;
  width: 100%;
  height: 100%;
  background : #fff;
}
.list-shortcut{
  position :absolute;
  z-index :30;
  right :0;
  top : 52%;
  transform :translateY(-50%);
  width:30px;
  // padding:20px 0;
  border-radius :50%;
  text-align :center;
  // background :#fff;
  font-size 0px
}
.list-shortcut .item{
  padding:2px;
  line-height :1;
  color : #666;
  font-size 14px;
}
.list-shortcut .avtive{
   color : #ccc;
   font-size 18px
}
.tongxunlu{
  height: 100%;
  width: 100%;
  background: #fff;
  overflow :hidden;
}

.scroo{
  position : relative;
  height :100%;
  overflow :hidden;
}
.list-group:last-child{
  padding-bottom :78px;
}
.list-group-title{
  height :28px;
  line-height :28px;
  padding-left :20px;
  background :#eee;
  // position: absolute;
  // z-index 100;
  // width :100%;
  // overflow hidden
}
.list-group-item{
  margin-left :30px;
  height: 40px;
  overflow: hidden;
  padding: 5px 0 5px 0px
  border-1px(#eee)

}

.list-group-item img{
  border-radius :50%;
  height: 100%;
  vertical-align:middle;
}
.list-group-item .name{
  // line-height: 30px;
  font-size: 16px;
}
</style>



