<template>
  <div class="singer">
  	<tongxunlu @getgeshou='sellgeshou' :data='singers'></tongxunlu>
	   <router-view></router-view>
  </div>
</template>

<script>

import BScroll from 'better-scroll'
import {getgeshou} from '../../api/recommend'
import tongxunlu from '../../base/tongxunlu/hello'
import {mapState,mapMutations} from 'vuex'

export default {
  name: 'hello',
  data () {
    return {
     	singers : {},
    }
  },
  created() {
 	this._sings()

  },
  components : {
  	tongxunlu
  },
  methods : {

    sellgeshou(i) {

       this.$router.push({
         path : `/singer/${i.id}`
       })

      this.setsinger(i)


    },
  	_sings() {
  		getgeshou().then((res)=>{
  			this.singers = res.data.list
  			this.singers = this._geshoufenlei(this.singers)
  		})
  	},

  	_geshoufenlei(list){
  		let map = {
  			hot : {
  				title :'热门',
  				items : []
  			}
  		}
  		list.forEach((item,index)=>{
  			if(index<10){
  				map.hot.items.push({
  					id:item.Fsinger_mid,
  					name: item.Fsinger_name,
  					avatar : `http://y.gtimg.cn/music/photo_new/T001R150x150M000${item.Fsinger_mid}.jpg?max_age=2592000`
  				})
  			}
  			const key = item.Findex
  			if(!map[key]){
  				map[key] = {
  					title : key,
  					items : []
  				}
  			}
  			map[key].items.push({
  					id:item.Fsinger_mid,
  					name: item.Fsinger_name,
  					avatar : `http://y.gtimg.cn/music/photo_new/T001R150x150M000${item.Fsinger_mid}.jpg?max_age=2592000`
  			})
  		})
  			let hot = []
  			let ret = []
  			for(let key in map){
  				let val = map[key]
  				if(val.title.match(/[a-zA-Z]/)){
  					ret.push(val)
  				} else if(val.title === '热门'){
  					hot.push(val)
  				}
  			}
  			ret.sort((a,b)=>{
  				return a.title.charCodeAt(0)-b.title.charCodeAt(0)
  			})
  			return hot.concat(ret)
  	},

    ...mapMutations(['setsinger'])

  },
  computed : {
//    ...mapState(["singer"])
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="stylus" rel="stylesheet/stylus" scoped>
	.singer
		height 100%

</style>



