<template>
  <div class="slider" ref='slider'>
    <div class="slider-group" ref='slidergroup'>
      <slot></slot>
    </div>
    <div class="dots">
      <span :class="{'active':currindex ===ind }" class="dot" v-for='(it,ind) in dots'></span>
    </div>
  </div>
</template>

<script>

import BSscoll from 'better-scroll'
import {addclass} from '../../commnt/js/dom'


export default {
  name: 'slider',
  data () {
    return {
      dots : [],
      currindex : 0,
      timer : ''
    }
  },
  props : {
    loop : {
      default : true
    },
    aotopaly : {
       default : true
    },
    intime : {
       default : 1000
    },
  },
  created() {
  	
  },
  methods : {
  	_play() {
      let pageinde = this.currindex + 1
        if(this.loop){
          pageinde +=1
        }
        this.timer = setTimeout(()=>{
          this.slider.goToPage(pageinde,0,400)
          // console.log(1)
        },this.intime)
    },
    _aa(isrsize) {
       this.children = this.$refs.slidergroup.children
      let width = 0
      let sliderwith = this.$refs.slider.clientWidth
       // console.log(sliderwith)
       // console.log(sliderwith)
      for(let i = 0;i<this.children.length;i++){
        let child = this.children[i]
        addclass(child,'slider-item')
        // console.log(width)
        child.style.width = sliderwith+'px'
          width +=sliderwith

      } 
      if(this.loop && !isrsize){
        width += 2 * sliderwith
      }
     
      this.$refs.slidergroup.style.width = width +'px'
    },
    _bb() {
      this.dots = new Array(this.children.length);
    },
    _cc(){
       this.slider = new BSscoll(this.$refs.slider,{
        scrollX : true,
        scrollY : false,
        momentum : false,
        snap : true,
        snapLoop : this.loop,
        snapThreshold : 0.3,
        snapSpeed : 400,
        snapThreshold : 0.6
      })
       this.$refs.slider.addEventListener('touchmove',()=>{
          clearTimeout(this.timer)
      })
      this.slider.on('scrollEnd',()=>{
        // console.log('scrollEnd')
        let pageindex = this.slider.getCurrentPage().pageX
        if(this.loop){
          pageindex -= 1
        }
        this.currindex = pageindex
        // console.log(pageindex)
        // 
        if(this.aotopaly){
          clearTimeout(this.timer)
          this._play()
        }
      })
     
    }
  },
  mounted() {
    setTimeout(()=>{
      this._aa()
      this._bb()
      this._cc()
      if(this.aotopaly){
        this._play()
      }
    },20)

    window.addEventListener('resize',()=>{
      if(!this.slider){
         return
       }
       this._aa(true)
       this.slider.refresh()
    })

   

  },
  activated() {
    this._play()
  },
 deactivated() {
   clearTimeout(this.timer)
 }

  
}
</script>
	
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="stylus" rel="stylesheet/stylus" scoped>
  .slider
    min-height 1px
    position relative
    .slider-group
      position relative
      overflow hidden
      white-space nowrap
      .slider-item
        float left
        box-sizing border-box
        overflow hidden
        text-align center
        a
          display block
          width 100%
          overflow hidden
          text-decoration none
        img
          display block
          width 100%
    .dots
      position absolute
      // right 0px
      // left 0
      bottom -33px
      width 100%
      // top 0px
      text-align center
      font-size 0
      height 50px
      .dot
        display inline-block
        margin 0 4px
        width 8px
        height 8px
        border-radius 50%
        background rgba(144,144,144,0.8)
        &.active
          // width 20px
          // border-radius 5px
          background #fff
</style>



