<template>
  <transition name="move">
  	<div  class="geshouxiangqing">
        <div :style="bg" class="geshouxiangqing-1" ref="scro2">
          <i @click="$router.push('/singer')" class="iconfont icon-qiehuanqizuo"></i>
          <p>{{name}}</p>
        </div>
        <div  class="scrol" ref="scrol">
          <gequList :geshouxixin="geshouxixin"></gequList>
        </div>

  </div>
  </transition>
</template>

<script>
import gequList from '../../base/gequ-list/hello'
import {mapState} from 'vuex'
import {getgequliebiao} from '../../api/recommend'
import BSscroll from 'better-scroll'

export default {
  name: 'move',
  data () {
    return {
      geshouxixin : [],
      imgur: '',
      mp4:'' ,
      name :'',
      scolly:'',
    }
  },
  computed : {
    ...mapState(["singer"]),
    bg() {
        return `background-size:cover;background-image:url(${this.imgur});`
    },
//    bg1() {
//      return 'background-size:cover'
//    },

  },
  created() {
    this._getgequliebiao()
  },
  methods : {


    _getgequliebiao(){
        if(!this.singer.id){
            this.$router.push('/singer')
            return
        }
      getgequliebiao(this.singer.id).then((res)=>{
          this.geshouxixin=res.data.list
//          console.log(res)
//          console.log( this.geshouxixin)
          this.imgur = `https://y.gtimg.cn/music/photo_new/T001R300x300M000${this.geshouxixin[0].musicData.singer[0].mid}.jpg?max_age=2592000`
          this.name = this.geshouxixin[0].musicData.singer[0].name
          this.mp4 = `http://dl.stream.qqmusic.qq.com/C400${this.geshouxixin[0].musicData.songmid}.m4a?fromtag=38&vkey=54B56386EDBF074CD703A49938B11A07FFE3F8BAE29D04DAB1E95839E50DB9A777FEFE069143A430D45EACFA758565C3EA38FBB2EB0775BC&guid=9509601598`

      })

    },
    _sclorr(){
        this.scroll = new BSscroll(this.$refs.scrol,{
          probeType: 3,
          click: true
        })
      this.scroll.on('scroll',(pos)=>{
         this.scolly =Math.round(pos.y)
         this.$refs.scro2.style.height=this.scolly+'px'
      })
    }
  },
  components : {
    gequList
  },
  watch : {
    geshouxixin() {
      setTimeout(()=>{
        this.$nextTick(()=>{

          this._sclorr()

        })
      },1)
    }
  },
  mounted() {
      this.$refs.scrol.style.height = document.body.clientHeight-this.$refs.scro2.clientHeight+'px'
//      console.log(document.body.clientHeight)
//      console.log(this.$refs.scro2)
//      console.log(this.$refs.scrol)
  },

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="stylus" rel="stylesheet/stylus" scoped>

	.geshouxiangqing
		position fixed
		z-index 110
		// height 300px
		top	0px
		left 0px
		right 0px
		bottom 0px
		background #fff
	.move-enter-active, .move-leave-active
		transition all 0.4s
	.move-enter, .move-leave-to
		transform translate3d(100%,0,0)
.geshouxiangqing-1{
  position: relative
  width 100%
  height 0
  padding-top: 80%
  /*z-index 200*/
  /*transform-origin top*/
  /*background-size 320px 100%*/
}
.geshouxiangqing-1 i,.geshouxiangqing-1 p{
  position absolute
  top 1px
  color #fff


}
  .geshouxiangqing-1 i{
    padding 15px

   }
  .geshouxiangqing-1 p{
    margin-left 10px
     top 85%
    /*width 100%*/
    /*text-align center*/
    font-size 24px
    color #000
  }
  .scrol{
    /*height 100%*/
  }
</style>



