export function addclass(el,className){//添加一个类
	if(hasclass(el,className)){
		return
	}
	let newClass = el.className.split('')
	newClass.push(className)
	el.className = newClass.join('')

}



export function hasclass(el,className){//检测资格类是否存在
	let reg = new RegExp('(^|\\s)'+className + '(\\s|$)')
	return reg.test(el.className)
}





let elstyle = document.createElement('div').style//js加的样式自动加前缀

let vendor = (() => {
  let tranfname = {
    webkit: "webkitTransform",
    Moz : "MozTransfrom",
    O :'OTransfrom',
    ms:'msTransfrom',
    standard :'Transfrom'
  }
  for(let key in tranfname) {
    if(elstyle[tranfname[key]]!==undefined){
      return key
    }
  }
  return false
})()

export function prestyle(style) {
  if(vendor === false){
    return false
  }
  if(vendor === 'standard'){
    return style
  }
  return vendor + style.charAt(0).toUpperCase() + style.substr(1)
}



