import 'babel-polyfill'
import Vue from 'vue'
import App from './App'
import router from './router'
import store from './store'

import falaclick from 'fastclick'
import Vuelaz from 'vue-lazyload'



Vue.config.productionTip = false

falaclick.attach(document.body)

// console.log(store)

Vue.use(Vuelaz,{
	loading: require('./commnt/img/logo.png')
})
/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  render: h => h(App)
})



