<template>
  <div class="recommend"  >
  	<div ref='waepp' class='waepp'>
			<div>
				 <div class="recommend-contend" v-if='recommends.length'>
			 			<slider>
			 				<div v-for="item in recommends" >
								<a :href="item.linkUrl">
								<img @load='loadpage' :src="item.picUrl">
								</a>
			 				</div >
			 			</slider>
				</div>
				<div class="recommend-list">
					<h1 class="list-title">热门歌单</h1>
					<ul>
						<li class="item" @click='clicktext' v-for='item in disclist'>
							<div class="icon">
								<img v-lazy="item.imgurl" height="60" width="60">
							</div>
							<div class="text">
								<h2 class="name" v-html='item.creator.name'></h2>
								<p class="desc" v-html='item.dissname'></p>
							</div>
						</li>
					</ul>
				</div>
			</div>
			<div class="loading-container" v-show="!disclist.length">
					<langing></langing>
			</div>
		</div>
	</div>
</template>

<script>

import slider from '../../base/slider/hello'
import langing from '../../base/langing/hello'
import scrool from 'better-scroll'
import {getrecojson,getgedan} from '../../api/recommend'


// let url = 'https://c.y.qq.com/splcloud/fcgi-bin/p.fcg'

// let data = {
// 	g_tk:5381,
// 	format:'jsonp',
// }
// let opti = {
// 	param : 'jsonpCallback'
// }


// console.log(Url(url,data))

// Jsonp(Url(url,data),opti,(data)=>{
// 	console.log(data)
// })
// console.log(Promise)


export default {
  name: 'recommend',
  data () {
    return {
    	recommends :[],
    	disclist : [],
    	iflond : true,
    }
  },
  created() {
  	this._getrecojson()
  	this.	_getdeda()


  },
  mounted() {
	this.$nextTick(()=>{
	this.ongundong()

  	})
  },
  methods : {
  	clicktext() {
  		// console.log(1)
  	},
  	loadpage() {
  		if(this.iflond){
  			this.gun.refresh()
  			this.iflond = false
  		}

  	},
  	_getrecojson() {
  		getrecojson().then((res)=>{
  			// console.log(res.data.slider)
  			this.recommends = res.data.slider
  		})
  	},
  	_getdeda() {
  		this.disclist = getgedan().list
  		// console.log(this.disclist)
  	},
  	ongundong() {
  			this.gun = new scrool(this.$refs.waepp,{
  				// click:true
  			})
  			// console.log(this.gun)
  	}

  },
  components : {
  	slider,
  	langing

  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="stylus" rel="stylesheet/stylus" scoped>
	.recommend
		// position fixed
		// top 40px

		width 100%
		overflow hidden
		height 100%
		background #eee
		.waepp
			height 100%
			overflow hidden
			.recommend-list
				.list-title
					height 44px
					line-height 44px
					text-align center
					font-size 15px
				.item
					display flex
					box-sizing border-box
					align-items center
					padding 10px 10px 10px 10px
					border-right 10px solid #eee
					border-left 10px solid #eee
					background #fff
					margin-bottom 6px
					&:last-child
						padding-bottom 78px
					.icon
						flex 0 0 60px
						width 60px
						padding-right 20px
					.text
						display flex
						flex-direction column
						justify-content center
						flex 1
						line-height 20px
						overflow hidden
						font-size 14px
						.name
							margin-bottom 10px
						.desc
							color #999
		.loading-container
			position absolute
			width 100%
			top 50%
			transform translateY(-50%)
</style>



