import Vue from 'vue'
import Router from 'vue-router'

import recommend from '../components/recommend/Hello'
import singer from '../components/singer/Hello'
import rank from '../components/rank/Hello'
import search from '../components/search/Hello'
import geshouxiangqing from '../components/geshouxiangqing/Hello'


Vue.use(Router)

export default new Router({
  routes: [
  	{
  		path : '/',
  		redirect : '/recommend'
  	},
    {
      path: '/recommend',
      name: 'recommend',
      component: recommend
    },
    {
      path: '/singer',
      name: 'singer',
      component: singer,
      children: [
        {
          path : ':id',
          component: geshouxiangqing
        }
      ]
    },
    {
      path: '/rank',
      name: 'rank',
      component: rank
    },
    {
      path: '/search',
      name: 'search',
      component: search
    }
  ]
})



