export function getgequliebiao(geshouid) {
  const url = 'https://c.y.qq.com/v8/fcg-bin/fcg_v8_singer_track_cp.fcg'
  const data = {
    cid:205360581,
    singermid:geshouid,
    order:'listen',
    begin:0,
    num:100,
    g_tk:5381,
    loginUin:0,
    hostUin:0,
    format:'jsonp',
    inCharset:'utf8',
    outCharset:'utf-8',
    notice:0,
    platform:'yqq',
    needNewCode:0
  }

  return jsonp(url,data,options)
}





export function getgeshou() {
    const url = 'https://c.y.qq.com/v8/fcg-bin/v8.fcg'
    const data = {
        channel:'singer',
        page:'list',
        key:'all_all_all',
        pagesize:100,
        pagenum:1,
        g_tk:5381,
        loginUin:0,
        hostUin:0,
        format:'jsonp',
        inCharset:'utf-8',
        outCharset:'utf-8',
        notice:0,
        platform:'yqq',
        needNewCode:0,
    }

    return jsonp(url,data,options)
}








export function getrecojson() {
	const url = 'https://c.y.qq.com/musichall/fcgi-bin/fcg_yqqhomepagerecommend.fcg'
	const data = {
		g_tk:5381,
		format:'json',
		inCharset:'utf-8',
		outCharset:'utf-8',
		notice:0,
		uin:0,
		platform:'h5',
		needNewCode:1
	}

	return jsonp(url,data,options)
}


